@layer base {
  * {
    border-color: var(--border);
    outline-color: var(--ring);
    @supports (color: color-mix(in lab, red, red)) {
      outline-color: color-mix(in oklab, var(--ring) 50%, transparent);
    }
  }
  body {
    background-color: var(--background);
    color: var(--foreground);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background: linear-gradient(135deg, #ffffff 0%, #f8fafc 50%, #f1f5f9 100%);
    min-height: 100vh;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, 'Noto Sans', sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Sans Devanagari';
  }
  .hindi-text {
    font-family: 'Noto Sans Devanagari', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
    font-feature-settings: 'kern' 1;
    text-rendering: optimizeLegibility;
  }
  [lang="hi"] {
    font-family: 'Noto Sans Devanagari', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
    line-height: 1.7;
  }
  [lang="en"] {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
    line-height: 1.6;
  }
  html {
    scroll-behavior: smooth;
  }
  ::-webkit-scrollbar {
    width: 8px;
    height: 8px;
  }
  ::-webkit-scrollbar-track {
    background: rgba(148, 163, 184, 0.1);
    border-radius: 4px;
  }
  ::-webkit-scrollbar-thumb {
    background: rgba(19, 136, 8, 0.3);
    border-radius: 4px;
  }
  ::-webkit-scrollbar-thumb:hover {
    background: rgba(19, 136, 8, 0.5);
  }
  @keyframes fade-in {
    from {
      opacity: 0;
      transform: translateY(10px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  @keyframes slide-in-left {
    from {
      opacity: 0;
      transform: translateX(-20px);
    }
    to {
      opacity: 1;
      transform: translateX(0);
    }
  }
  @keyframes pulse-glow {
    0%, 100% {
      box-shadow: 0 0 15px rgba(19, 136, 8, 0.1);
    }
    50% {
      box-shadow: 0 0 20px rgba(19, 136, 8, 0.15);
    }
  }
  .government-gradient {
    background: linear-gradient(135deg, #138808 0%, var(--gov-saffron) 50%, var(--gov-navy) 100%);
  }
  .government-card {
    border-top: 3px solid var(--gov-saffron);
    box-shadow: 0 4px 20px -4px rgba(19, 136, 8, 0.1);
  }
  .government-badge {
    background: linear-gradient(45deg, var(--gov-saffron), #ffb366);
    color: var(--gov-navy);
    font-weight: 600;
  }
  .animate-fade-in {
    animation: fade-in 0.6s ease-out;
  }
  .animate-slide-in-left {
    animation: slide-in-left 0.5s ease-out;
  }
  .animate-pulse-glow {
    animation: pulse-glow 2s ease-in-out infinite;
  }
}
